import { Grid, StyledEngineProvider, ThemeProvider } from "@mui/material";
import DetailMessage from "page/DetailMessage";
import React, { useCallback, useEffect, useReducer, useState } from "react";
import { QueryClient, QueryClientProvider } from "react-query";
import { appContext, AppData, appData } from "./context";
import Noti from "./page/Noti";
import UserInfo from "./page/UserInfo";
import { theme } from "./theme";
import { Helmet } from "react-helmet";

const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      retry: false,
      suspense: false,
      useErrorBoundary: false,
      cacheTime: 1000 * 60 * 60 * 24,
      refetchOnWindowFocus: false,
    },
  },
});

function App() {
  const [a, setA] = useState(false);
  const [state, dispatch] = useReducer((state: any, action: any) => {
    switch (action.type) {
      case "SET_STATE":
        return {
          ...state,
          ...action.payload,
        };
      default:
        return { ...state };
    }
  }, appData);
  const setState = useCallback((state: Omit<AppData, "setState">) => {
    dispatch({
      type: "SET_STATE",
      payload: state,
    });
  }, []);

  useEffect(() => {
    const url = new URL(window.location.href);
    const timeout = +(url.searchParams.get("timeout") || 500);
    const p = new Promise((re) => setTimeout(() => re(true), timeout));
    p.then(() => setA(true));
  }, []);

  return (
    <StyledEngineProvider injectFirst>
      <ThemeProvider theme={theme}>
        <QueryClientProvider client={queryClient}>
          <appContext.Provider value={{ ...state, setState }}>
            <Helmet>
              <meta property="og:title" content="Title of your webpage" />
              <meta
                property="og:description"
                content="Description of your webpage"
              />
              {a && <meta property="og:image" content="https://homepage.momocdn.net/img/momo-upload-api-240307143829-638454191095958901.jpg" />}
            </Helmet>
            <Grid container spacing={2}>
              <Grid item xs={12} md={6} lg={4}>
                <UserInfo />
              </Grid>
              <Grid item xs={12} md={6} lg={4}>
                <Noti />
              </Grid>
              <Grid item xs={12} md={6} lg={4}>
                <DetailMessage />
              </Grid>
            </Grid>
          </appContext.Provider>
        </QueryClientProvider>
      </ThemeProvider>
    </StyledEngineProvider>
  );
}

export default App;

const aa =
  "data:image/jpeg;base64,";
